import { buildSchema } from 'graphql';

export const schema = buildSchema(`
  type Message {
    id: ID!
    content: String!
    role: String!
    timestamp: String!
  }

  type ChatResponse {
    message: String!
    usage: Usage
  }

  type Usage {
    prompt_tokens: Int
    completion_tokens: Int
    total_tokens: Int
  }

  type Query {
    # 健康检查
    health: String!
    
    # 获取支持的模型列表
    models: [String!]!
  }

  type Mutation {
    # 发送聊天消息
    sendMessage(
      message: String!
      model: String = "deepseek-chat"
      temperature: Float = 0.7
      max_tokens: Int = 2000
    ): ChatResponse!
    
    # 批量发送消息（支持对话历史）
    sendConversation(
      messages: [MessageInput!]!
      model: String = "deepseek-chat"
      temperature: Float = 0.7
      max_tokens: Int = 2000
    ): ChatResponse!
  }

  input MessageInput {
    role: String!
    content: String!
  }
`);